"use client";

import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { AnimatePresence, motion } from "framer-motion";
import { Fredoka } from "next/font/google";
import { useEffect, useMemo, useState } from "react";
import "./home-screen-v3.css";

const fredoka = Fredoka({
  subsets: ["latin"],
  weight: ["500", "600", "700"],
});

const BANNERS = [
  {
    id: "1",
    title: "Crowd Crush Tonight",
    subtitle: "Friends vote fast, Toast picks the winner.",
    cta: "Open Game Room",
    href: "/game-setup?preset=crowd-crush",
  },
  {
    id: "2",
    title: "Lightning Lunch",
    subtitle: "3-minute timer with high-energy swiping.",
    cta: "Play 3-Min",
    href: "/swipe?mode=quick-lunch",
  },
  {
    id: "3",
    title: "Explore by Mood",
    subtitle: "Chill, spicy, cozy, or bold - pick your vibe.",
    cta: "Start Explore",
    href: "/explore?source=mood",
  },
];

const NAV_ITEMS = [
  { href: "/home-screen-v3", label: "Home", icon: "HM" },
  { href: "/explore", label: "Explore", icon: "EX" },
  { href: "/map", label: "Map", icon: "MP" },
  { href: "/appointment-setup", label: "Plan", icon: "PL" },
  { href: "/preferences-setup", label: "Profile", icon: "PF" },
];

const TRENDY = [
  { id: "t1", title: "Street Food Showdown", meta: "Top group likes in your area", href: "/swipe?preset=street" },
  { id: "t2", title: "Comfort Food Pulse", meta: "Popular after 7 PM", href: "/swipe?preset=comfort" },
  { id: "t3", title: "Protein Power Picks", meta: "High-rated healthy picks", href: "/swipe?preset=protein" },
];

const SHORTCUTS = [
  { id: "s1", title: "No Beef Mode", detail: "Dietary lock on", href: "/swipe?shortcut=no-beef", skip: true },
  { id: "s2", title: "Near Transit", detail: "Distance constraint", href: "/game-setup?shortcut=transit", skip: false },
  { id: "s3", title: "Dessert Dash", detail: "Cafe and bakery boost", href: "/swipe?shortcut=dessert", skip: true },
];

export default function HomeScreenV3Page() {
  const pathname = usePathname();
  const [activeBanner, setActiveBanner] = useState(0);
  const [hasCurrentGame] = useState(true);

  useEffect(() => {
    const timer = setInterval(() => {
      setActiveBanner((prev) => (prev + 1) % BANNERS.length);
    }, 3600);

    return () => clearInterval(timer);
  }, []);

  const banner = useMemo(() => BANNERS[activeBanner], [activeBanner]);

  return (
    <div className={`${fredoka.className} hv3-root`}>
      <div className="hv3-phone">
        <div className="hv3-page">
          <div className="hv3-status">
            <span>9:41</span>
            <span className="hv3-battery" aria-hidden="true">
              <span className="hv3-battery__level" />
              <span className="hv3-battery__cap" />
            </span>
          </div>

          <div className="hv3-content">
            <div className="hv3-shell">
              <header className="space-y-3">
                <div className="flex items-center justify-between">
                  <Link href="/preferences-setup" className="flex items-center gap-2 rounded-2xl border border-zinc-200 bg-zinc-50 px-3 py-2">
                    <Image src="/toast-logo.png" alt="Profile" width={34} height={34} className="rounded-lg border border-zinc-200" />
                    <div>
                      <p className="text-[10px] uppercase tracking-[0.15em] text-zinc-500">Profile</p>
                      <p className="text-base leading-4 font-bold text-zinc-900">Open Preferences</p>
                    </div>
                  </Link>
                  <div className="rounded-full bg-orange-100 px-3 py-1 text-xs font-bold text-orange-700">Live x12</div>
                </div>

                <div>
                  <p className="text-[11px] uppercase tracking-[0.18em] text-zinc-500">Good evening</p>
                  <h1 className="mt-1 text-4xl leading-8 font-bold text-zinc-900">Let&apos;s settle food faster.</h1>
                </div>

                <div className="hv3-quick-strip">
                  <button className="hv3-quick-pill hv3-quick-pill--hot">Hot: 3 Min Mode</button>
                  <button className="hv3-quick-pill">All Match</button>
                  <button className="hv3-quick-pill">Nearby 2 km</button>
                  <button className="hv3-quick-pill">Include Friends Prefs</button>
                </div>

                <label className="flex items-center gap-2 rounded-2xl border border-zinc-200 bg-zinc-50 px-3 py-2.5">
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" className="h-4 w-4 text-zinc-500">
                    <circle cx="11" cy="11" r="7" strokeWidth="2" />
                    <path d="M20 20l-3.5-3.5" strokeWidth="2" strokeLinecap="round" />
                  </svg>
                  <input type="text" placeholder="Search area or restaurant" className="w-full bg-transparent text-sm text-zinc-800 placeholder:text-zinc-400 focus:outline-none" />
                  <Link href="/explore" className="rounded-lg bg-zinc-900 px-2.5 py-1 text-xs font-bold text-white">
                    Go
                  </Link>
                </label>
              </header>

              <section className="hv3-banner mt-4 p-4">
                <AnimatePresence mode="wait">
                  <motion.div key={banner.id} initial={{ opacity: 0, y: 8 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0, y: -8 }} transition={{ duration: 0.25 }}>
                    <p className="text-xs uppercase tracking-[0.16em] text-orange-700">Banner</p>
                    <h2 className="mt-1 text-3xl leading-7 font-bold text-zinc-900">{banner.title}</h2>
                    <p className="mt-2 text-base leading-5 text-zinc-700">{banner.subtitle}</p>
                    <Link href={banner.href} className="mt-3 inline-flex rounded-full bg-zinc-900 px-3 py-1.5 text-xs font-bold text-white">
                      {banner.cta}
                    </Link>
                  </motion.div>
                </AnimatePresence>

                <div className="mt-3 flex gap-1.5">
                  {BANNERS.map((item, index) => (
                    <button
                      key={item.id}
                      aria-label={`banner-${index + 1}`}
                      onClick={() => setActiveBanner(index)}
                      className={`h-2 rounded-full transition-all ${activeBanner === index ? "w-7 bg-zinc-900" : "w-2 bg-zinc-300"}`}
                    />
                  ))}
                </div>
              </section>

              <section className="mt-4 grid grid-cols-3 gap-2">
                <Link href="/swipe?mode=menu" className="rounded-2xl border border-zinc-200 bg-zinc-50 px-2 py-3 text-center">
                  <p className="text-lg">MN</p>
                  <p className="text-xs font-bold text-zinc-900">Menu</p>
                </Link>
                <Link href="/appointment-setup" className="rounded-2xl border border-zinc-200 bg-zinc-50 px-2 py-3 text-center">
                  <p className="text-lg">AP</p>
                  <p className="text-xs font-bold text-zinc-900">Appointment</p>
                </Link>
                <Link href="/explore" className="rounded-2xl border border-zinc-200 bg-zinc-50 px-2 py-3 text-center">
                  <p className="text-lg">EX</p>
                  <p className="text-xs font-bold text-zinc-900">Explore</p>
                </Link>
              </section>

              {hasCurrentGame && (
                <section className="mt-4 rounded-2xl border border-emerald-200 bg-emerald-50 p-3.5">
                  <p className="text-xs uppercase tracking-[0.14em] text-emerald-700">Current game</p>
                  <h3 className="mt-1 text-2xl leading-7 font-bold text-zinc-900">Friday Food Squad</h3>
                  <p className="mt-1 text-sm text-zinc-700">4 members - host deciding mode - 00:58 left</p>
                  <Link href="/swipe?resume=true" className="mt-3 inline-flex rounded-full bg-emerald-700 px-3 py-1.5 text-xs font-bold text-white">
                    Resume Game
                  </Link>
                </section>
              )}

              <section className="hv3-mission mt-4 p-3">
                <p className="text-[11px] uppercase tracking-[0.15em] text-amber-700">Daily mission</p>
                <p className="text-sm font-bold text-zinc-900">Complete one group match to unlock confetti blast.</p>
              </section>

              <section className="mt-4">
                <div className="mb-2 flex items-center justify-between">
                  <h3 className="text-xs uppercase tracking-[0.18em] text-zinc-700">Trendy picks</h3>
                  <Link href="/explore" className="text-xs font-bold text-orange-600">View all</Link>
                </div>
                <div className="space-y-2">
                  {TRENDY.map((item) => (
                    <Link key={item.id} href={item.href} className="block rounded-2xl border border-zinc-200 bg-white px-3 py-2.5">
                      <p className="text-lg leading-5 font-bold text-zinc-900">{item.title}</p>
                      <p className="text-sm text-zinc-500">{item.meta}</p>
                    </Link>
                  ))}
                </div>
              </section>

              <section className="mt-4 pb-6">
                <h3 className="mb-2 text-xs uppercase tracking-[0.18em] text-zinc-700">Game shortcuts</h3>
                <div className="space-y-2">
                  {SHORTCUTS.map((item) => (
                    <Link key={item.id} href={item.href} className="flex items-center justify-between rounded-2xl border border-zinc-200 bg-zinc-50 px-3 py-3">
                      <div>
                        <p className="text-lg leading-5 font-bold text-zinc-900">{item.title}</p>
                        <p className="text-sm text-zinc-600">{item.detail}</p>
                      </div>
                      <span className={`rounded-full px-2 py-1 text-[10px] font-bold ${item.skip ? "bg-orange-100 text-orange-700" : "bg-zinc-200 text-zinc-700"}`}>
                        {item.skip ? "Skip setup" : "Setup"}
                      </span>
                    </Link>
                  ))}
                </div>
              </section>
            </div>
          </div>

          <Link
            href="/game-setup"
            className="absolute bottom-[86px] right-4 z-20 inline-flex h-14 w-14 items-center justify-center rounded-full bg-orange-500 text-2xl text-white shadow-[0_16px_30px_rgba(249,115,22,0.4)]"
            aria-label="Create game room"
          >
            +
          </Link>

          <nav className="hv3-nav" aria-label="Bottom navigation">
            {NAV_ITEMS.map((item) => {
              const active = pathname === item.href;
              return (
                <Link key={item.href} href={item.href} className={`hv3-nav__item ${active ? "hv3-nav__item--active" : ""}`}>
                  <span className="hv3-nav__icon" aria-hidden="true">{item.icon}</span>
                  <span className="hv3-nav__label">{item.label}</span>
                </Link>
              );
            })}
          </nav>
        </div>
      </div>
    </div>
  );
}

